import { retrieveEnvVariable } from '../infra/retrieveEnvVariable';
const defaultBackendPort = '8000';
const defaultBackendFqdn = 'localhost';
const defaultBackendSheme = 'http';
export const backendPort = (envVariableValue, envVariableName) => parseInt(retrieveEnvVariable(envVariableValue, envVariableName, defaultBackendPort));
export const backendFqdn = (envVariableValue, envVariableName) => retrieveEnvVariable(envVariableValue, envVariableName, defaultBackendFqdn);
export const backendSheme = (envVariableValue, envVariableName) => retrieveEnvVariable(envVariableValue, envVariableName, defaultBackendSheme);
